@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,500;1,600;1,700;1,800;1,900&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  font-family: helvetica;
}

.pj-main-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  height: 100vh;
  background-color: lightgray;

  * {
    font-family: 'Bebas Neue', cursive;
  }
}

// Header
.pj-header {
  display: flex;
  align-items: center;
  height: 10vh;
  min-height: fit-content;
  width: 100%;
  background-color: black;
  padding: 10px 25px;

  h1 {
    flex-grow: 1;
    font-size: 50px;
    text-align: center;
    color: red;
  }

  .pj-back-button {
    justify-self: flex-start;
    text-decoration: none;
    color: #e50914;
  }

  .pj-back-button:hover {
    color: white;
  }
}

// Project Info
.pj-project-info {
  display: flex;
  height: fit-content;
  width: 100%;
  
  h2, p {
    font-family: 'Raleway', sans-serif;
  }

  .project-details, .project-description {
    display: flex;
    flex-direction: column;
    flex-grow: 0;
    width: 100%;
    padding: 25px;
    color: white;

    h2 {
      margin-bottom: 10px;
    }
  }

  .project-details {
    background-color: #e50914;

    .external-links {
      margin-top: 20px;
      display: flex;
      justify-content: space-between;

      i {
        color: white;
      }

      i:hover {
        color: black;
      }
    }
  }

  .project-description {
    background-color: #b20710;
  }
}

// Main Content
.pj-main-content {
  flex-grow: 1;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
  min-height: fit-content;
  padding: 20px 5px;
  background-color: lightgray;

  .pj-side-menu {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    height: fit-content;
    width: fit-content;
    margin: 0px 10px;
    overflow-x: scroll;
    padding: 10px 5px;
    border-radius: 10px;
    background-color: rgba(0,0,0,0.6);

    .project-icon-container {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 100%;
      background-color: transparent;
      margin: 5px 15px;
      border: none;
      color: white;
      text-shadow: 2px 2px black; 
      text-align: center;

      button {
        font-family: helvetica;
        background-color: red;
        color: white;
        font-weight: 900;
        width: 80%;
        padding: 2px 5px;
        border: 1px solid white;
        border-radius: 25px;
        margin-top: 5px;
      }

      button:hover {
        background-color: white;
        color: red;
        cursor: pointer;
      }

      i {
        margin-bottom: 5px;
      }
    }
  }

  .pj-tv-screen {
    border: 15px solid darkgrey;
    border-radius: 15px;
    height: 563px;
    width: 1000px;

    .home-button {
      height: 10px;
      width: 10px;
      background-color: white;
      border-radius: 100%;
      position: relative;
      left: 50%;
      top: 0.5%;
    }
  }
}

// Caroursel
.pj-carousel-page {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  height: 533px;
  background-size: cover;
  background-position: center;
  padding-bottom: 0px;
  background-color: deepskyblue;

  .page-legend {
    padding: 10px;
    width: 50%;
    background-color: rgba(0,0,0, 0.5);
    opacity: 1;
    margin-bottom: 50px;

    p {
      color: white;
      text-shadow: 2px 2px black;
      font-family: 'Raleway', sans-serif;
      font-weight: 700;
    }
  }
}

// Media Queries
@media screen and (max-width: 1200px) {
  .pj-project-info {
    height: unset;
    flex-grow: 0;
  }

  .pj-main-content {
    flex-direction: column;
    height: fit-content;
    padding-bottom: 20px;

    .pj-side-menu {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      flex-wrap: wrap;
      margin-bottom: 5px;

      .project-icon-container {
        width: 100px;
        margin: 5px 15px;
        
        button {
          width: 100%;
        }
      }
    }
    .pj-tv-screen {
      height: 400px;
      width: 711px;
    }
  }

  .pj-carousel-page {
    height: 370px;
  }
}

@media screen and (max-width: 980px) {
  .pj-project-info {

    .project-details, .project-description {
      h2 {
        font-size: 25px;
      }
      
      p {
        font-size: 15px;
      }
    }
  }
}

@media screen and (max-width: 890px) {
  .pj-project-info {
    .project-details, .project-description {

      h2 {
        font-size: 20px;
      }

      p {
        font-size: 13px;
      }
    }
  }
}

@media screen and (max-width: 820px) {
  .pj-main-content {
    padding-top: 0px;
    padding-left: 0px;
    padding-right: 0px;
    padding-bottom: 20px;
    justify-content: unset;

    .pj-side-menu {
      width: 100%;
      border-radius: 0px;
      margin-top: 0px;
      justify-content: space-evenly;

      .project-icon-container {
        width: 50px;
        margin: 0;

        i {
          font-size: 10px;
          margin-bottom: 10px;
        }

        button {
          width: 90px;
          font-size: 8px;
        }
      }
    }

    .pj-tv-screen {
      margin: auto 0px;
      width: 338px;
      height: 600px;
    }

    .pj-carousel-page {
      height: 570px;
    }
  }
}

@media screen and (max-width: 645px) {
  .pj-main-content {
    height: fit-content;

    .pj-side-menu {
      margin-bottom: 20px;

      .project-icon-container {
        width: 30%;
      }
    }

    .pj-tv-screen {
      width: 281px;
      height: 500px;
    }

    .pj-carousel-page {
      height: 470px;
    }
  }

  .pj-project-info {
    height: unset;
  }
}

@media screen and (max-width: 515px) {
  .pj-main-content {
    .pj-tv-screen {
      height: 400px;
      width: 225px;
    }

    .pj-carousel-page {
      height: 370px;

      .page-legend {
        display: none;
      }
    }
  }

  .pj-project-info {
    flex-wrap: wrap;
    .project-details, .project-description {
      width: 100%;
      padding: 20px 10px;
    }
  }
}

@media screen and (max-width: 420px) {
  .pj-header {
    h1 {
      font-size: 40px;
    }
    
    i {
      font-size: 40px;
    }
  }

  .pj-main-content {
    .pj-tv-screen {
      width: 174px;
      height: 310px;
    }

    .pj-carousel-page {
      height: 280px;
    }
  }
}
